.promo__title {
  margin: 0;
  padding-top: 40px;
  padding-left: 14px;
  padding-right: 20px;
  text-align: center;

  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 21px;
    padding-top: 50px;
    padding-left: 160px;
    padding-right: 155px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 345px;
    padding-right: 345px;
  }
}

.promo__text {
  margin: 0;
  padding-top: 28px;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
  font-size: 14px;
  line-height: 26px;
  font-weight: normal;
  text-transform: uppercase;
}
